.chat {
    display: flex;
    flex-direction: column; 
    flex: 0.75; 
    background-color: #363a3f;
    height: 100vh;
}

.chat__messages{
    flex: 1;
    overflow: scroll;
}

/*Hide scrollbar for Chrome, Safari and Opera */
.chat__messages::-webkit-scrollbar{
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.chat__messages {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none;  /* Firefox*/
}

.chat__input {
    color: gray;
    display: flex;
    align-items: center;
    justify-content: space-between; 
    padding: 15px;
    border-radius: 5px;
    margin: 20px;
    border-top: 1px solide gray;
    background-color: #474b53;
}

.chat__input > form > input {
    padding: 15px;
    background: transparent;
    border: none;
    width: 100%;
    outline-width: 0ch;
    color: white;
    font-size: large;
}

.chat__input > form {
    flex:1;
}

.chat__inputButton {
    display: none;
}

.chat__inputIcons > .MuiSvgIcon-root{
    padding: 5px;
}



